.main-container-circle{
    height: 500px;
    margin: 90px;
}

.chart-index-container{
    display: flex; 
    margin-left: 160px;
    justify-content: space-evenly;
    column-gap: 19px;
}

.chart-index{
    margin-bottom: 60px;
}

.chart-data-plot-container{
    list-style-type: none;
    padding-left: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 700px;
    overflow: auto; 
}

.chart-data-plot{
    display: flex; 
    align-items: center;
}

.chart-left-index{
    width: 160px;
}

.chart-plots-container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    list-style-type: none;
    padding-left: 0px;
    width: calc(70% - 1px);
    column-gap: 19px;
}

.chart-plots{
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    cursor: pointer;
    
}

.chart-val{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}


